@import url('https://fonts.googleapis.com/css2?family=Eagle+Lake&family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


* {
	box-sizing: border-box;
    font-family: 'Eagle Lake', serif;
    font-family: 'Work Sans', sans-serif;
}

.auth-body {
    background-image: url('../components/bg1.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
	background-position: center center;
	height: 100vh;
	width: 100vw;
	overflow: hidden;
}

.loadpage-wrapper {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-family: 'Montserrat', sans-serif;
	margin: 50px 50px 0 0;
}

.crracc {
	font-weight: bold;
	margin: 0;
}

h2 {
	text-align: center;
}

p {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

span {
	font-size: 12px;
	font-family: 'Kanit', Arial, sans-serif;
}

a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

button {
	border: 1px solid white;
	background-color: #252d3d;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	margin-bottom: 10px;
	margin-left: 10px;
	font-family: 'Kanit', Arial, sans-serif;
}

button:active {
	transform: scale(0.95);
}

button:focus {
	outline: none;
}

button.ghost {
	background-color: transparent;
	border-color: #FFFFFF;
}

form {
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
	border-radius: 30px;
}

.containerone {
	background-color: #fff;
	border-radius: 10px;
	box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25),
		0 10px 10px rgba(0, 0, 0, 0.22);
	position: relative;
	overflow: hidden;
	width: 768px;
	max-width: 100%;
	min-height: 480px;
}

.form-container {
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.sign-in-container {
	left: 0;
	width: 50%;
	z-index: 2;
}

.containerone.right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.containerone.right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	animation: show 0.6s;
}

@keyframes show {

	0%,
	49.99% {
		opacity: 0;
		z-index: 1;
	}

	50%,
	100% {
		opacity: 1;
		z-index: 5;
	}
}

.overlay-container {
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.containerone.right-panel-active .overlay-container {
	transform: translateX(-100%);
}

.overlay {
	background: linear-gradient(to right, #404154, #7cafb2);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.containerone.right-panel-active .overlay {
	transform: translateX(50%);
}

.overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.overlay-left {
	transform: translateX(-20%);
}

.containerone.right-panel-active .overlay-left {
	transform: translateX(0);
}

.overlay-right {
	right: 0;
	transform: translateX(0);
}

.containerone.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.social-container {
	margin: 20px 0;
}

.social-container a {
	border: 1px solid #DDDDDD;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	height: 40px;
	width: 40px;
}

footer {
	background-color: #222;
	color: #fff;
	font-size: 14px;
	bottom: 0;
	position: fixed;
	left: 0;
	right: 0;
	text-align: center;
	z-index: 999;
}

footer p {
	margin: 10px 0;
}

footer i {
	color: red;
}

footer a {
	color: #3c97bf;
	text-decoration: none;
}

.contactList {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Kanit', Arial, sans-serif;
	font-size: 15px;
}

.dashHead {
	font-family: 'Kanit', Arial, sans-serif;
	font-size: 35px;
}

.edDel button {
	--c: #229091;

	box-shadow: 0 0 0 .1em inset var(--c);
	--_g: linear-gradient(var(--c) 0 0) no-repeat;
	background:
		var(--_g) calc(var(--_p, 0%) - 100%) 0%,
		var(--_g) calc(200% - var(--_p, 0%)) 0%,
		var(--_g) calc(var(--_p, 0%) - 100%) 100%,
		var(--_g) calc(200% - var(--_p, 0%)) 100%;
	background-size: 50.5% calc(var(--_p, 0%)/2 + .5%);
	outline-offset: .1em;
	transition: background-size .4s, background-position 0s .4s;
}

.edDel button:hover {
	--_p: 100%;
	transition: background-position .4s, background-size 0s;
}

.edDdel button:active {
	box-shadow: 0 0 9e9q inset #0009;
	background-color: var(--c);
}

.edDel button {
	font-family: 'Kanit', Arial, sans-serif;
	cursor: pointer;
	border: none;
	color: #000;
}

.addCon {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Kanit', Arial, sans-serif;
	font-size: 20px;
}

.contactListBody {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Kanit', Arial, sans-serif;
	font-size: 15px;
}

.addform {
	background-color: black;
}

.webTitle {
	margin-top: 60px;
}
